#navbar {
  width: 5rem !important;
  padding-left: 1rem !important;
  padding-right: 1rem !important;
  /* background-color: #7166f9; */
  background-color: #23272a;
}

.logo {
  width: 5rem !important;
  /* height: 5rem !important;  */
  height: 10% !important;
  background: #7166f9;
}

.logo .navbar-icon {
  font-size: 2.5rem;
  color: #fff;
}

.navbar-icon{
    font-size: 1.75rem;
    color: #fff;
}

.navbar-item {
  width: 4rem;
  height: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #99aab5;
  border-radius: 2rem;
  margin-top: 1rem;
}

.navbar-item-active{
    background: #7166f9;
}

.navbar-item:hover {
    width: 4rem;
    height: 4rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: #7166f9;
    border-radius: 1.5rem;
  }