.freelancer-card{
    position: relative;
    width: 18rem !important;
    height: 18rem !important;
    border-radius: 1rem;
}

.freelancer-photo{
    width: 5rem !important;
    height: 5rem !important;
    object-fit: cover;
    border-radius: 2.5rem !important;
}

button{
    box-shadow: none !important;
}

button:focus{
    box-shadow: none !important;
    border-width: 0 !important;
}

#status-indicator{
    position: absolute;
    top: 10%;
    right: 10%;
    background: #0F0;
    width: 1rem;
    height: 1rem;
    border-radius: 0.5rem;
}

.user-active{
    background: #0f0 !important;
}

.user-inactive{
    background: #f00 !important;
}
.profile-container{
    width: 35% !important;
    max-width: 35% !important;
}

.profile-modal-header{
    border: none !important;
}

.profile-title{
    color: #7166f9;
    font-weight: bold;
}

.profile-footer{
    border: none !important;
}

.profile-body{
    overflow-y: scroll;
}

.proflie-pic{
    height: 10rem;
    width: 10rem;
    border-radius: 5rem;
    object-fit: cover;
}
.gallery-image{
    width: 33% !important;
    height: 15% !important;
    height: 10rem !important;
    object-fit: cover;
}